section.side-bar-box {
  width: 15%;
  background: #1d2733;
  letter-spacing: 1px;
  padding-top: 1.5rem;
}

ul.side-bar-list {
  list-style: none;
  position: -webkit-sticky;
  position: sticky;
  top: 13%;
}

li.side-bar-list-item {
  color: #fff;
  padding: 12px 6px;
  margin: 0px 0 0 10px;
  font-size: 13px;
}

li.side-bar-list-item:hover {
  background-color: #2986b862;
}

.active {
  background-color: #2986b8;
}
