.main-top-box {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.5rem;
  border: none;
  -webkit-box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.21);
          box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.21);
}

.main-top-box-text-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

span.main-top-box-text {
  font-size: 1.3rem;
  font-weight: 700;
}

p.main-top-box-text-bottom {
  font-size: .9rem;
}

.main-top-box-text-icon {
  font-size: 1.7rem;
  font-weight: 700;
}
