@import "../ProjectStyleVariables/_style.scss";

header {
    background: $themeColor;
    display: flex;
    padding: 5px 12px;
    align-items: center;
    justify-content: space-between;
    color: $LightColor;
    position: sticky;
    top: 0;
    z-index: 1;

    .header-tabs {
        display: flex;
    }

    .tabs {
        margin: 0 5px 0;
        font-size: 12px;
        padding: 7px;
        display: flex;
        align-items: center;
    }
}


