.main-box {
  width: 85%;
  padding: 10px;
  background-color: #f7f7f7;
}

.boxShadow {
  border-radius: 5px;
  border: none;
  background: #fff;
  -webkit-box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.21);
          box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.21);
}

.main-box-inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  gap: 10px;
}

.main-box-inner-second {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 1rem 0;
  gap: 10px;
}

.main-box-inner-second-colOne {
  width: 35%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 10px;
}

.main-box-inner-second-colTwo {
  width: 65%;
}

.colOne-first {
  text-align: center;
  padding: 7px;
}

span.ColTest {
  font-size: .9rem;
  font-weight: 700;
}

.colOne-first > span {
  margin: 0 6px;
}

span.ColInput > input {
  width: 55px;
  padding: 4px;
}

.min-box-inner-head {
  background: #5f5f861f;
  padding: 7px;
  font-size: 15px;
  font-weight: 500;
}

.barChart-box, .barChart-box-low {
  padding: 1rem;
}

#record-select {
  padding: 3px;
  border-radius: 6px;
}

.min-box-inner-head-semi {
  padding: 7px;
  font-size: 15px;
  text-align: center;
  font-weight: 700;
}

.main-box-inner-third {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 10px;
  width: 100%;
}

.main-box-inner-third-colTwo {
  width: 50%;
}
